import React from 'react';
import { getServerSession } from "next-auth";
import { authOptions } from "@/lib/auth";
import Project from "@/models/Project";
import Experience from "@/models/Experience";
import connectToDatabase from "@/lib/db";

async function getStats() {
    await connectToDatabase();
    const projectCount = await Project.countDocuments();
    const experienceCount = await Experience.countDocuments();
    return { projectCount, experienceCount };
}

export default async function AdminDashboard() {
    const session = await getServerSession(authOptions);
    const { projectCount, experienceCount } = await getStats();

    return (
        <div className="space-y-8">
            <header>
                <h1 className="text-4xl font-display text-primary mb-2">Dashboard</h1>
                <p className="text-secondary">Welcome back, {session?.user?.name || 'Admin'}.</p>
            </header>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <div className="bg-surface border border-border p-6 rounded-lg">
                    <h3 className="text-sm font-medium text-secondary mb-2">Total Projects</h3>
                    <p className="text-4xl font-display text-primary">{projectCount}</p>
                </div>
                <div className="bg-surface border border-border p-6 rounded-lg">
                    <h3 className="text-sm font-medium text-secondary mb-2">Experience Entries</h3>
                    <p className="text-4xl font-display text-primary">{experienceCount}</p>
                </div>
                <div className="bg-surface border border-border p-6 rounded-lg">
                    <h3 className="text-sm font-medium text-secondary mb-2">System Status</h3>
                    <div className="flex items-center gap-2 mt-2">
                        <div className="w-3 h-3 bg-green-500 rounded-full animate-pulse"></div>
                        <span className="text-primary">Operational</span>
                    </div>
                </div>
            </div>
        </div>
    );
}
